import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { postBackendAPI } from "../../utils/altdnsUtils/altDnsGeneratorFunctions";
import {
  unResolvedDnsNetworkIssueAction,
  reResolvedDnsAction,
} from "../../../actions/altdnsActions/altDnsGeneratorActions";
import { setLocalStorage } from "../../utils/setLocalStorage";

import { toast } from "react-toastify";

class ModalBody extends Component {
  resubmitNetworkIssueDnsFunc = async (e) => {
    e.preventDefault();

    let confirmResolveDns = window.confirm("Confirm Re-Resolve AltDns");

    if (confirmResolveDns) {
      // Control BTN While DNS Resolve Is In Progress
      document
        .getElementById("resolveDns")
        .setAttribute("disabled", "disabled");
      document.getElementById("resolveDns").style.opacity = "0.2";

      // Control BTN While DNS Resolve Is In Progress
      document.getElementById("networkIssueBtn").style.display = "none";

      console.log(
        "----this.props.unResolvedDnsNetworkIssue:",
        this.props.unResolvedDnsNetworkIssue
      );

      // Resolve DNS
      let post2BackendAPI = await postBackendAPI(
        this.props.unResolvedDnsNetworkIssue
      );

      let resolvedDomain = post2BackendAPI.resolvedDomains;
      let resolvedDomainLength = resolvedDomain.length;

      console.log("post2BackendAPI-2:", post2BackendAPI);

      if (resolvedDomainLength > 0) {
        // Add Newly Resolved DNS Count To Badge
        document.getElementById("altDnsBadge2").textContent =
          Number(document.getElementById("altDnsBadge2").textContent) +
          resolvedDomainLength;

        // Save Resolved  DNS To State & LS
        this.props.reResolvedDnsAction(resolvedDomain);

        let resolvedDnsLS = JSON.parse(localStorage.getItem("resolvedDNS"));
        let reResolvedDns = [...resolvedDnsLS, ...resolvedDomain];

        setLocalStorage("resolvedDNS", reResolvedDns);

        resolvedDomain.forEach((element) => {
          // Add reResolved To 2nd View
          document.getElementById("resolvedDns").textContent += `${element}\n`;
        });
      }

      // networkIssue
      let networkIssue = post2BackendAPI.networkIssue;
      let networkIssueLength = networkIssue.length;

      setLocalStorage("unResolvedDnsNetworkIssue", networkIssue);

      console.log("networkIssue:", networkIssue);
      console.log("networkIssue.length:", networkIssue.length);

      document.getElementById("networkIssuesBadge2").textContent =
        networkIssueLength;
      if (networkIssueLength > 0) {
        document.getElementById("networkIssuesModalForm").value = "";
        // Display To networkIssuesModalForm
        networkIssue.forEach(
          (element) =>
            (document.getElementById(
              "networkIssuesModalForm"
            ).value += `${element}\n`)
        );

        this.props.unResolvedDnsNetworkIssueAction(networkIssue);
        document.getElementById("networkIssueBtn").style.display = "block";
      } else {
        document.getElementById("networkIssuesModalForm").value = "";
        this.props.unResolvedDnsNetworkIssueAction(networkIssue);
      }

      // Control BTN While DNS Resolve Is Complected & Display Success Message
      document.getElementById("resolveDns").removeAttribute("disabled");
      document.getElementById("resolveDns").style.opacity = "1";
      toast.success("DNS Successfully Resolved");
    }
  };

  render() {
    return (
      <div
        id="networkIssuesModal"
        className="modal fade"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-scrollable modal-dialog-centered">
          <div
            className="modal-content bg-dark text-light"
            style={{ border: "1px solid white", borderRadius: "4%" }}
          >
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">
                Unresolved DNS Due To Network Issues
              </h5>
              <button
                type="button"
                className="btn-close bg-light"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <form
                onSubmit={this.resubmitNetworkIssueDnsFunc}
                className=" form"
              >
                <div className="form-group">
                  <textarea
                    id="networkIssuesModalForm"
                    className="form-control bg-danger text-light"
                    style={{
                      boxShadow:
                        "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                      borderRadius: "35px",
                      fontSize: "larger",
                      padding: "5px 2px 2px 25px",
                    }}
                    rows="15"
                  ></textarea>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-bs-dismiss="modal"
                    >
                      Close
                    </button>
                    <button
                      type="submit"
                      className="btn btn-primary"
                      style={{
                        boxShadow:
                          "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                      }}
                      onClick={this.resolveDnsAgain}
                      data-bs-dismiss="modal"
                    >
                      Resolve Dns Again...
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ModalBody.propType = {
  unResolvedDnsNetworkIssueAction: PropTypes.array.unResolvedDnsNetworkIssue,
  unResolvedDnsNetworkIssue: PropTypes.array.unResolvedDnsNetworkIssue,
};

const mapStateToProps = (state) => ({
  unResolvedDnsNetworkIssue:
    state.altDns.altDnsGenerator.unResolvedDnsNetworkIssue,
});

export default connect(mapStateToProps, {
  unResolvedDnsNetworkIssueAction,
  reResolvedDnsAction,
})(ModalBody);
