import { SUBDOMAIN_WILD_CARD_LOCATOR } from "../../actions/wildcardActions/types";

const initialState = {
  totalSubdomains: [],
  wildCardAreas: [],
};

export default function potentialSubdomainWildcardAreaLocatorReducer(
  state = initialState,
  action
) {
  switch (action.type) {
    case SUBDOMAIN_WILD_CARD_LOCATOR:
      return {
        ...state,
        totalSubdomains: action.payload.totalSubdomains,
        wildCardAreas: action.payload.wildCardAreas,
      };
    default:
      return state;
  }
}
