import React, { Component } from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";

import ModalBody from "./components/layouts/altdnsLayouts/ModalBody";
import About from "./pages/About";
import Disclaimer from "./pages/Disclaimer";
import NotFound from "./pages/NotFound";
import Footer from "./components/layouts/Footer";

import store from "./store";
import "./app.css";

import AltDnsApp from "./components/layouts/altdnsLayouts/AltDnsApp";
import WildCardApp from "./components/layouts/wildcardLayouts/WildCardApp";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <ToastContainer />
          <ModalBody />
          <Switch>
            <Route exact path="/" component={WildCardApp} />
            <Route exact path="/altDnsApp" component={AltDnsApp} />
            <Route exact path="/about" component={About} />
            <Route exact path="/disclaimer" component={Disclaimer} />
            <Route component={NotFound} />
          </Switch>
        </Router>
        <Footer />
      </Provider>
    );
  }
}

export default App;
