import { createStore, applyMiddleware, compose, combineReducers } from "redux";
import thunk from "redux-thunk";
import altdnsReducers from "./reducers/altdnsReducers";
import wildcardReducers from "./reducers/wildcardReducers";

const initialState = {};

const middleware = [thunk];

// const actionSanitizer = (action) =>
//   action.type === "ALT_DNS" && action.payload.length > 1000
//     ? { ...action, payload: "<<LONG_BLOB>>" }
//     : action;

const rootReducer = combineReducers({
  wildCard: wildcardReducers,
  altDns: altdnsReducers,
});

const store = createStore(
  rootReducer,
  initialState,
  compose(
    applyMiddleware(...middleware)
    // window.__REDUX_DEVTOOLS_EXTENSION__ // Disable REDUX_DEVTOOLS_EXTENSION To Prevent Memory Overload
    //   ? window.__REDUX_DEVTOOLS_EXTENSION__()
    //   : // {
    //     //   actionSanitizer,
    //     //   stateSanitizer: (state) =>
    //     //     state.altDnsGenerator.altDnsList
    //     //       ? { ...state.altDnsGenerator, altDnsList: "<<LONG_BLOB>>" }
    //     //       : state,
    //     // }s

    //     (f) => f
  )
);

export default store;
