import JSnumberToWordProcessor from "js-number-to-word-processor";

export async function display2ViewTEMP(
  totalSubdomains,
  combinedSubdomain,
  totalPotentialWildCardArea,
  combinedPotentialWildCardArea,
  LSsubdomains,
  LSwildCardArea
) {
  let compiledSubdomains = LSsubdomains;
  let perCompiledSubdomainsLength = String(compiledSubdomains.length);
  let compiledSubdomainsLength;
  if (
    !perCompiledSubdomainsLength.includes("e") ||
    !perCompiledSubdomainsLength.includes("+")
  ) {
    compiledSubdomainsLength = await JSnumberToWordProcessor(
      perCompiledSubdomainsLength
    )[0].displayWholeNum;
  } else {
    compiledSubdomainsLength = perCompiledSubdomainsLength;
  }
  let compiledPotentialWildCardArea = LSwildCardArea;
  let preCompiledPotentialWildCardAreaLength = String(
    compiledPotentialWildCardArea.length
  );
  let compiledPotentialWildCardAreaLength;

  if (
    !preCompiledPotentialWildCardAreaLength.includes("e") ||
    !preCompiledPotentialWildCardAreaLength.includes("+")
  ) {
    compiledPotentialWildCardAreaLength = await JSnumberToWordProcessor(
      preCompiledPotentialWildCardAreaLength
    )[0].displayWholeNum;
  } else {
    compiledPotentialWildCardAreaLength =
      preCompiledPotentialWildCardAreaLength;
  }

  document.getElementById(totalSubdomains).textContent =
    compiledSubdomainsLength;

  document.getElementById(totalPotentialWildCardArea).textContent =
    compiledPotentialWildCardAreaLength;

  document.getElementById(combinedSubdomain).textContent = "";
  if (compiledSubdomainsLength > 1500) {
    document.getElementById(combinedSubdomain).textContent =
      "For Optimization Sake! Output Greater Than 1500 Can Only Be Viewed From Console";
  } else {
    compiledSubdomains.forEach((element) => {
      document.getElementById(combinedSubdomain).textContent += `${element}\n`;
    });
  }
  // +++++++++++++++++++++++++++ Display Potential WildCard +++++++++++++++++++++++++++++
  document.getElementById("combinedPotentialWildCardArea").textContent = "";
  if (compiledPotentialWildCardAreaLength > 1500) {
    document.getElementById(combinedPotentialWildCardArea).textContent =
      "For Optimization Sake! Output Greater Than 1500 Can Only Be Viewed From Console";
  } else {
    compiledPotentialWildCardArea.forEach((element) => {
      document.getElementById(
        combinedPotentialWildCardArea
      ).textContent += `${element}\n`;
    });
  }
  // +++++++++++++++++++++++++++ Display Potential WildCard +++++++++++++++++++++++++++++

  // ((((((((((((((((((((((((((((((((( DISPLAY BADGE )))))))))))))))))))))))))))))))))
  document.getElementById("wildCardBadge1").innerText =
    compiledSubdomainsLength;
  document.getElementById("wildCardBadge2").innerText =
    compiledPotentialWildCardAreaLength;
  // ((((((((((((((((((((((((((((((((( DISPLAY BADGE )))))))))))))))))))))))))))))))))

  return {
    totalSubdomains: compiledSubdomains,
    wildCardAreas: compiledPotentialWildCardArea,
  };
}

