import {
  ALT_DNS,
  DNS,
  CHECK_BOX,
  RESOLVED_DNS,
  UNRESOLVED_DNS_NETWORK_ISSUE,
  RE_RESOLVED_DNS,
} from "./types";

import altDnsGen from "../../components/utils/altdnsUtils/altDnsGenerator";

export const altDnsListGen = (wordList, dnsList, slim) => async (dispatch) => {
  let generatedDns = altDnsGen(wordList, dnsList, slim);
  dispatch({
    type: ALT_DNS,
    payload: generatedDns,
  });
};

export const altDnsListGenTEMP = (altDnsList) => async (dispatch) => {
  dispatch({
    type: ALT_DNS,
    payload: altDnsList,
  });
};

export const dnsInputListGen = (wordList, dnsList) => async (dispatch) => {
  dispatch({
    type: DNS,
    payload: {
      wordList,
      dnsList,
    },
  });
};

export const checkBoxGen = () => async (dispatch) => {
  dispatch({
    type: CHECK_BOX,
  });
};

export const resolvedDnsAction = (dns) => async (dispatch) => {
  dispatch({
    type: RESOLVED_DNS,
    payload: dns,
  });
};

export const unResolvedDnsNetworkIssueAction =
  (unresolvedDns) => async (dispatch) => {
    dispatch({
      type: UNRESOLVED_DNS_NETWORK_ISSUE,
      payload: unresolvedDns,
    });
  };

export const reResolvedDnsAction = (reResolvedDns) => async (dispatch) => {
  dispatch({
    type: RE_RESOLVED_DNS,
    payload: reResolvedDns,
  });
};
