import React, { Component } from "react";
import MainBody from "./MainBody";
import SideBar from "./SideBar";

class Body extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <SideBar />
          <MainBody />
        </div>
      </div>
    );
  }
}

export default Body;
