import React, { Component } from "react";
import { Link } from "react-router-dom";

export class LinkTo extends Component {
  render() {
    const { page, title, subTitle } = this.props;
    return (
      <Link to={page} className="clearfix mb-2">
        <button
          className="navbar-brand bg-dark"
          style={{ fontStyle: "italic", fontSize: "2.4em" }}
        >
          {title}
          <span
            className="float-end me-3"
            style={{
              marginTop: "-0.08rem",
              marginLeft: ".2rem",
              color: "lightgray",
              fontSize: "20px",
            }}
          >
            {subTitle}
          </span>
        </button>
      </Link>
    );
  }
}

export class LinkToBody extends Component {
  render() {
    const { title, subTitle, paragraph } = this.props;
    return (
      <div
        className="container row col-sm-6 card me-4"
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.2)",
          color: "lightgray",
          boxShadow:
            "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
        }}
      >
        <h1
          className="display-4 p-2 mb-4 text-center"
          style={{
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          }}
        >
          {title} <br />
          <p className="text-secondary h3">{subTitle}</p>
        </h1>
        <p className="lead h4">{paragraph}</p>
        <hr />
        <h6 className="text-secondary">
          Developed With React.js, Redux, &amp; Express.js.
          <br />
          <br />
          <a
            target="_blank"
            rel="noreferrer"
            href=" https://github.com/Sidodus"
          >
            https://github.com/Sidodus
          </a>
        </h6>
        <hr />
      </div>
    );
  }
}
