import { toast } from "react-toastify";

export const setLocalStorage = (keyName, value) => {
  try {
    localStorage.setItem(keyName, JSON.stringify(value));
  } catch (error) {
    // Display Notification & Maintain Whatever Is In LS
    toast.error(
      "Local Storage Quota Exceeded: \n Ensure You Download Or Copy ReSult Before Changing This Page"
    );

    // Display Only The "notifyMe__" Icon
    if (keyName === "potentialWildcardLocator") {
      document.getElementById("notifyMeOfTotalDns").style.display = "block";
      document.getElementById("notifyMeOfWildcards").style.display = "block";
    } else if (keyName === "altDnsGen") {
      document.getElementById("notifyMeOfUnresolvedDns").style.display =
        "block";
    } else if (keyName === "resolvedDns") {
      document.getElementById("notifyMeOfResolvedDns").style.display = "block";
    }
  }
};
