import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { toast } from "react-toastify";

import { saveAs } from "file-saver";
import {
  saveFile,
  displayResolvedDns,
  postBackendAPI,
} from "../../utils/altdnsUtils/altDnsGeneratorFunctions";
import { setLocalStorage } from "../../utils/setLocalStorage";

import {
  resolvedDnsAction,
  unResolvedDnsNetworkIssueAction,
} from "../../../actions/altdnsActions/altDnsGeneratorActions";

class NavBar extends Component {
  saveUnresolvedDnsToFileEvent = () => {
    saveAs(
      saveFile(this.props.altDnsListProps, "SWcLAltDnsGenUnresolvedDns.txt")
    );
  };

  saveResolvedDns2FileEvent = () => {
    saveAs(saveFile(this.props.resolvedDns, "SWcLAltDnsGenResolvedDNS.txt"));
  };

  resolveDnsEvent = async () => {
    let confirmResolveDns = window.confirm("Confirm Resolve AltDns");

    if (confirmResolveDns) {
      // Control BTN While DNS Resolve Is In Progress
      document
        .getElementById("resolveDns")
        .setAttribute("disabled", "disabled");
      document.getElementById("resolveDns").style.opacity = "0.2";

      let post2BackendAPI;
      try {
        // Resolve DNS
        post2BackendAPI = await postBackendAPI(this.props.altDnsListProps);
      } catch (error) {
        post2BackendAPI = {
          resolvedDomains: ["NO CONNECTION TO SERVER!!!"],
          networkIssue: [],
        };
      }

      // Save Resolved  DNS To State & LS
      this.props.resolvedDnsAction(post2BackendAPI.resolvedDomains);
      setLocalStorage("resolvedDNS", post2BackendAPI.resolvedDomains);

      // Display To 2nd View
      displayResolvedDns(post2BackendAPI.resolvedDomains);

      // networkIssue
      let networkIssue = post2BackendAPI.networkIssue;
      let networkIssueLength = networkIssue.length;

      setLocalStorage("unResolvedDnsNetworkIssue", networkIssue);

      document.getElementById("networkIssuesBadge2").textContent =
        networkIssueLength;
      if (networkIssueLength > 0) {
        document.getElementById("networkIssuesModalForm").value = "";
        // Display To networkIssuesModalForm
        networkIssue.forEach(
          (element) =>
            (document.getElementById(
              "networkIssuesModalForm"
            ).value += `${element}\n`)
        );

        this.props.unResolvedDnsNetworkIssueAction(networkIssue);
        document.getElementById("networkIssueBtn").style.display = "block";
      }

      // Control BTN While DNS Resolve Is Complected & Display Success Message
      document.getElementById("resolveDns").removeAttribute("disabled");
      document.getElementById("resolveDns").style.opacity = "1";
      toast.success("DNS Successfully Resolved");
    }
  };

  render() {
    return (
      <React.Fragment>
        <nav
          className="navbar navbar-expand-lg navbar-nav-scroll fixed-top navbar-dark bg-dark"
          aria-label="Main navigation"
        >
          <div className="container-fluid clearfix">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              AltDns Generator.js
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (02)
              </span>
            </button>
          </div>
          {/* ABOUT */}
          <div className="nav nav-underline">
            <Link
              to="/about"
              className="nav-link float-end me-5"
              style={{
                fontStyle: "italic",
                fontSize: "1.7em",
              }}
            >
              About
            </Link>
          </div>
        </nav>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}
        <div className="nav-scroller bg-dark shadow-sm">
          <nav className="nav nav-underline" aria-label="Secondary navigation">
            <Link
              to="/"
              className="nav-link"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
            >
              Dashboard
            </Link>

            <button
              id="saveOutputToFile"
              className="nav-link ms-auto bg-dark"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
              onClick={this.saveUnresolvedDnsToFileEvent}
            >
              Save{" "}
              <span
                id="altDnsBadge1"
                className="badge bg-light text-dark rounded-pill align-text-bottom"
              ></span>{" "}
              Un-Resolved DNS To File
              <div
                id="notifyMeOfUnresolvedDns"
                className="float-end ms-1"
                title="Result Not Saved To Storage"
                style={{
                  width: "20px",
                  height: "80%",
                  backgroundColor: "red",
                  border: "1px solid red",
                  borderRadius: "100%",
                  display: "none",
                }}
              ></div>
            </button>

            <button
              className="nav-link bg-dark ms-auto me-5"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
              id="resolveDns"
              onClick={this.resolveDnsEvent}
            >
              Resolve DNS{" "}
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.8rem",
                  marginLeft: ".2rem",
                  // color: "darkgray",
                  fontSize: "18px",
                }}
              >
                (03)
              </span>
              <div
                id="notifyMeOfResolvedDns"
                className="float-end ms-1"
                title="Result Not Saved To Storage"
                style={{
                  width: "20px",
                  height: "80%",
                  backgroundColor: "red",
                  border: "1px solid red",
                  borderRadius: "100%",
                  display: "none",
                }}
              ></div>
            </button>
            <button
              className="nav-link bg-dark ms-auto me-5"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
              id="saveResolvedDns2File"
              onClick={this.saveResolvedDns2FileEvent}
            >
              Save{" "}
              <span
                id="altDnsBadge2"
                className="badge bg-light text-dark rounded-pill align-text-bottom"
              ></span>{" "}
              Resolved DNS To File{" "}
            </button>
            <button
              type="button"
              id="networkIssueBtn"
              className="nav-link bg-danger text-light ms-auto me-5"
              style={{
                fontStyle: "italic",
                fontSize: "1.1em",
                display: "none",
              }}
              data-bs-toggle="modal"
              data-bs-target="#networkIssuesModal"
              onClick={this.networkIssues}
            >
              {/*  Modal BTN */}
              <span
                id="networkIssuesBadge2"
                className="badge bg-dark text-light rounded-pill align-text-bottom"
              ></span>{" "}
              Network Issues{" "}
            </button>
          </nav>
        </div>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}

        <nav
          className="navbar navbar-expand-sm mb-2 clearfix"
          style={{
            backgroundColor: "limegreen",
            display: "block",
            marginTop: "-10px",
          }}
        >
          <h5
            style={{
              fontStyle: "italic",
              fontSize: "large",
              marginBottom: "-6px",
              marginLeft: "20px",
            }}
          >
            <span className="ms-5">Total AltDNS Generated is: </span>
            <span
              id="totalDnsGenerated"
              className="ps-2 pe-2"
              style={{
                fontWeight: "lighter",
                fontSize: "2em",
                marginTop: "-5px",
              }}
            ></span>
          </h5>
        </nav>
      </React.Fragment>
    );
  }
}

NavBar.propType = {
  altDnsListProps: PropTypes.array.isRequired,
  resolvedDnsAction: PropTypes.array.isRequired,
  unResolvedDnsNetworkIssueAction: PropTypes.array.isRequired,
  unResolvedDnsNetworkIssue: PropTypes.array.unResolvedDnsNetworkIssue,
};

const mapStateToProps = (state) => ({
  altDnsListProps: state.altDns.altDnsGenerator.altDnsList,
  resolvedDns: state.altDns.altDnsGenerator.resolvedDns,
});

export default connect(mapStateToProps, {
  resolvedDnsAction,
  unResolvedDnsNetworkIssueAction,
})(NavBar);
