import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import {
  display2View,
  readFile,
} from "../../utils/wildcardUtils/PotentialWildcardFunctions";
import { potentialWildcardLocatorAction } from "../../../actions/wildcardActions/subdomainWildcardAreasActions";

import { setLocalStorage } from "../../utils/setLocalStorage";

class SideBar extends Component {
  uploadFile4Sublist3rInput = (e) => {
    e.preventDefault();

    readFile(e, "sublist3rInput");
  };

  uploadFile4AssetfinderInput = (e) => {
    e.preventDefault();

    readFile(e, "assetfinderInput");
  };

  uploadFile4AmassInput = (e) => {
    e.preventDefault();

    readFile(e, "amassInput");
  };

  formSubmitEvent = async (e) => {
    e.preventDefault();

    // Hide "Result Not Saved TO Storage" RED Notification
    document.getElementById("notifyMeOfTotalDns").style.display = "none";
    document.getElementById("notifyMeOfWildcards").style.display = "none";

    let sublist3rInput = document
      .getElementById("sublist3rInput")
      .value.split(/,|\n/g);
    let assetfinderInput = document
      .getElementById("assetfinderInput")
      .value.split(/,|\n/g);
    let amassInput = document.getElementById("amassInput").value.split(/,|\n/g);

    let result = await display2View(
      sublist3rInput,
      assetfinderInput,
      amassInput,
      "totalSubdomains",
      "combinedSubdomain",
      "totalPotentialWildCardArea",
      "combinedPotentialWildCardArea"
    );

    // Update State & LS With Results & With Various Input Values
    this.props.potentialWildcardLocatorAction(result);
    setLocalStorage("potentialWildcardLocator", result);
    // localStorage.setItem("potentialWildcardLocator", JSON.stringify(result));
    setLocalStorage("sublist3rInput", sublist3rInput);
    // localStorage.setItem("sublist3rInput", JSON.stringify(sublist3rInput));
    setLocalStorage("assetfinderInput", assetfinderInput);
    // localStorage.setItem("assetfinderInput", JSON.stringify(assetfinderInput));
    setLocalStorage("amassInput", amassInput);
    localStorage.setItem("amassInput", JSON.stringify(amassInput));
  };

  render() {
    return (
      <form
        id="form"
        className="col-md-3"
        style={{ backgroundColor: "rgb(197, 208, 219)" }}
        onSubmit={this.formSubmitEvent}
      >
        <div className="form-group input-group-sm">
          <label
            htmlFor="dnsListInput"
            className="h6"
            style={{ margin: "15px 0 7px 0" }}
          >
            Input Subdomains Into Below Fields:
          </label>
          <textarea
            id="sublist3rInput"
            className="form-control"
            rows="6"
            placeholder="Enter All Subdomains Of domain1.com"
          ></textarea>
          <input
            id="uploadFile4Sublist3rInput"
            type="file"
            className="form-control border bg-secondary mt-1 mb-4"
            name="DNS List"
            onChange={this.uploadFile4Sublist3rInput}
          />
        </div>

        <div className="form-group input-group-sm">
          <textarea
            id="assetfinderInput"
            className="form-control"
            rows="6"
            placeholder="Enter All Subdomains Of domain2.com"
          ></textarea>
          <input
            id="uploadFile4AssetfinderInput"
            type="file"
            className="form-control border bg-secondary mt-1 mb-4"
            name="DNS List"
            onChange={this.uploadFile4AssetfinderInput}
          />
        </div>

        <div className="form-group input-group-sm">
          <textarea
            id="amassInput"
            className="form-control"
            rows="6"
            placeholder="Enter All Subdomains Of domain3.com"
          ></textarea>
          <input
            id="uploadFile4AmassInput"
            type="file"
            className="form-control border bg-secondary mt-1 mb-3"
            name="DNS List"
            onChange={this.uploadFile4AmassInput}
          />
        </div>

        <button type="submit" className="btn btn-success btn-sm mb-2">
          Process Subdomains
        </button>
      </form>
    );
  }
}

SideBar.propType = {
  potentialWildcardLocatorAction: PropTypes.func.isRequired,
};

export default connect(null, {
  potentialWildcardLocatorAction,
})(SideBar);
