import React from "react";
import { Link } from "react-router-dom";
import { LinkTo } from "./LinkTo";

function NotFound() {
  return (
    <React.Fragment>
      <nav
        className="navbar navbar-expand-lg navbar-nav-scroll fixed-top navbar-dark bg-dark"
        aria-label="Main navigation"
        style={{ width: "100%", height: "100%" }}
      >
        <div className=" row ps-4">
          <LinkTo
            page="/"
            title="Subdomain Wildcard Locator.js"
            subTitle="(01)"
          />
          <LinkTo
            page="/altDnsApp"
            title="AltDns Generator.js"
            subTitle="(02)"
          />
          <LinkTo page="/altDnsApp" title="DNS Resolver" subTitle="(03)" />
          <LinkTo page="/about" title="About" subTitle="*" />
        </div>
        {/* NotFound */}
        <div
          className="container col-md-6 me-4"
          style={{
            color: "lightgray",
            boxShadow:
              "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          }}
        >
          <h4>
            <span className="text-danger display-4"> 404 </span>
            <br /> Page Not Found <hr />
          </h4>
          <p className="p-1">Sorry!!! The Page You Request Is Not Available.</p>
          <p className="small text-secondary pb-3">
            It might have been removed; had its name changed; or is temporarily
            unavailable. Please ensure the Web site address is spelled
            correctly, OR go to the <Link to="/">Home Page</Link>, OR use the
            Menu to navigate to a specific Page.
          </p>
        </div>
      </nav>
    </React.Fragment>
  );
}

export default NotFound;
