import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import {
  altDnsListGen,
  altDnsListGenTEMP,
  dnsInputListGen,
  checkBoxGen,
  resolvedDnsAction,
  unResolvedDnsNetworkIssueAction,
} from "../../../actions/altdnsActions/altDnsGeneratorActions";

import {
  initDisplay,
  displayResolvedDns,
} from "../../utils/altdnsUtils/altDnsGeneratorFunctions";

import { wordList } from "../../utils/altdnsUtils/dnsWordList";
import { dnsList } from "../../utils/altdnsUtils/dnsList";

import { setLocalStorage } from "../../utils/setLocalStorage";

import Body from "./Body";
import NavBar from "./NavBar";

class AltDnsApp extends Component {
  async componentDidMount() {
    if (!localStorage.getItem("altDnsGen")) {
      setTimeout(async () => {
        // Generate Alt DNS Save To State & Display To DOM
        this.props.dnsInputListGen(wordList, dnsList);
        await this.props.altDnsListGen(wordList, dnsList, "slim");
        initDisplay(this.props.altDnsListProps);

        // Upload Input Values Into Input Fields
        wordList.forEach((element) => {
          document.getElementById("dnsWordListInput").value += `${element}\n`;
        });
        dnsList.forEach((element) => {
          document.getElementById("dnsListInput").value += `${element}\n`;
        });

        // Save altDnsGen, altDnsCheckBox, wordList & dnsList To LS
        setLocalStorage("altDnsGen", this.props.altDnsListProps);
        setLocalStorage("altDnsCheckBox", this.props.altDnsCheckBox);
        setLocalStorage("altDnsWordList", wordList);
        setLocalStorage("altDnsDnsList", dnsList);

        setLocalStorage("unResolvedDnsNetworkIssue", []);
      }, 10);
    } else {
      setTimeout(async () => {
        // Control CheckBox State
        if (localStorage.getItem("altDnsCheckBox") === true)
          this.props.checkBoxGen();

        // Fetch  Alt DNS From LS & Save To State & Display To DOM
        let altDnsWordListLS = JSON.parse(
          localStorage.getItem("altDnsWordList")
        );
        let altDnsListLS = JSON.parse(localStorage.getItem("altDnsDnsList"));

        this.props.dnsInputListGen(altDnsWordListLS, altDnsListLS);
        await this.props.altDnsListGenTEMP(
          JSON.parse(localStorage.getItem("altDnsGen"))
        );

        initDisplay(JSON.parse(localStorage.getItem("altDnsGen")));

        if (!localStorage.getItem("resolvedDNS")) {
          setLocalStorage("resolvedDNS", []);
          displayResolvedDns([]);
        } else {
          this.props.resolvedDnsAction(
            JSON.parse(localStorage.getItem("resolvedDNS"))
          );

          displayResolvedDns(JSON.parse(localStorage.getItem("resolvedDNS")));
        }

        // Network Issue
        let networkIssue = JSON.parse(
          localStorage.getItem("unResolvedDnsNetworkIssue")
        );
        let networkIssueLength = networkIssue.length;

        document.getElementById("networkIssuesBadge2").textContent =
          networkIssueLength;
        if (networkIssueLength > 0) {
          document.getElementById("networkIssuesModalForm").value = "";
          // Display To networkIssuesModalForm
          networkIssue.forEach(
            (element) =>
              (document.getElementById(
                "networkIssuesModalForm"
              ).value += `${element}\n`)
          );

          this.props.unResolvedDnsNetworkIssueAction(networkIssue);
          document.getElementById("networkIssueBtn").style.display = "block";
        }
      }, 10);
    }
  }

  render() {
    return (
      <div>
        <NavBar />
        <Body />
      </div>
    );
  }
}

AltDnsApp.propType = {
  checkBoxGen: PropTypes.func.isRequired,
  checkBoxProps: PropTypes.bool.isRequired,
  altDnsListGen: PropTypes.func.isRequired,
  altDnsListGenTEMP: PropTypes.func.isRequired,
  dnsInputListGen: PropTypes.func.isRequired,
  altDnsListProps: PropTypes.array.isRequired,
  altDnsCheckBox: PropTypes.bool.isRequired,
  resolvedDnsAction: PropTypes.func.isRequired,
  unResolvedDnsNetworkIssueAction: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  checkBoxProps: state.altDns.altDnsGenerator.checkBox,
  altDnsListProps: state.altDns.altDnsGenerator.altDnsList,
  altDnsCheckBox: state.altDns.altDnsGenerator.checkBox,
});

export default connect(mapStateToProps, {
  checkBoxGen,
  altDnsListGen,
  altDnsListGenTEMP,
  dnsInputListGen,
  resolvedDnsAction,
  unResolvedDnsNetworkIssueAction,
})(AltDnsApp);
