import React, { Component } from "react";

class Footer extends Component {
  render() {
    const thisYear = new Date("2021").getFullYear();
    let presentYear = new Date().getFullYear();

    return (
      // <div>
      <div className="container-fluid">
        <div className="row">
          <div
            className="col-md-12"
            style={{ backgroundColor: "rgb(233, 242, 243)" }}
          >
            <footer
              style={{
                fontStyle: "italic",
                color: "rgb(75, 74, 74)",
                textAlign: "center",
                padding: "5px 0",
              }}
            >
              <div>
                Copyright (c){" "}
                <span id="copyRightYear">
                  {thisYear === presentYear
                    ? thisYear
                    : thisYear < presentYear
                    ? thisYear + " - " + presentYear
                    : null}{" "}
                </span>
                by{" "}
                <a
                  href="https://www.linkedin.com/in/saheed-odulaja-75111337"
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  Saheed
                </a>{" "}
              </div>
            </footer>
          </div>
        </div>
      </div>
      // </div>
    );
  }
}

export default Footer;
