import { setLocalStorage } from "../../utils/setLocalStorage";

export function combineSubDomains(sublist3r, assetfinder, amass) {
  let myPromise = new Promise(function (myResolve, myReject) {
    let domainName = "";
    let backupDomainName = "";
    let bigBackupDomainName = "";
    let newSubDomain = [];
    let sublist3r_filtered_length_withDups = 0;
    let assetfinder_filtered_length_withDups = 0;
    let amass_filtered_length_withDups = 0;

    function forEachSubdomain(subdomain, filtered_length_withDups) {
      subdomain.forEach((element) => {
        element = element.trim();

        let matchDotElement = element.match(/\./g);
        if (matchDotElement !== null) {
          if (matchDotElement.length > 1) {
            // Choose Domain Name If 2 Dots
            if (matchDotElement.length === 2 && element.match(/^www\./)) {
              if (backupDomainName === "") backupDomainName = element;
            } else {
              if (
                filtered_length_withDups ===
                "sublist3r_filtered_length_withDups"
              ) {
                sublist3r_filtered_length_withDups++;
              } else if (
                filtered_length_withDups ===
                "assetfinder_filtered_length_withDups"
              ) {
                assetfinder_filtered_length_withDups++;
              } else if (
                filtered_length_withDups === "amass_filtered_length_withDups"
              ) {
                amass_filtered_length_withDups++;
              }

              if (!newSubDomain.includes(element)) newSubDomain.push(element);
            }
            // ++++++++++++++++++++++++++++Bigger Domains+++++++++++++++++++++++++++++++++++++++++
            if (matchDotElement.length >= 2) {
              if (bigBackupDomainName === "") {
                bigBackupDomainName = element;
              } else if (
                bigBackupDomainName.match(/\./g).length > matchDotElement.length
              ) {
                bigBackupDomainName = element;
              }
            }
            // ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++
          } else {
            if (domainName === "") domainName = element;
          }
        }
      });
    }

    forEachSubdomain(sublist3r, "sublist3r_filtered_length_withDups");
    forEachSubdomain(assetfinder, "assetfinder_filtered_length_withDups");
    forEachSubdomain(amass, "amass_filtered_length_withDups");

    // Display Domain Name
    if (domainName === "" && backupDomainName !== "") {
      document.getElementById("domainName").innerText = backupDomainName;
      setLocalStorage("domainName", backupDomainName);
    } else if (domainName === "" && backupDomainName === "") {
      document.getElementById("domainName").innerText = bigBackupDomainName;
      setLocalStorage("domainName", bigBackupDomainName);
    } else {
      document.getElementById("domainName").innerText = domainName;
      setLocalStorage("domainName", domainName);
    }

    myResolve({
      newSubDomains: newSubDomain.sort(),
      sublist3r_filtered_length_withDups,
      assetfinder_filtered_length_withDups,
      amass_filtered_length_withDups,
    });
    myReject("An Error Occurred @ combineSubDomains Function");
  });
  return myPromise.then(
    (success) => success,
    (error) => error
  );
}

