import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";

import {
  display2View,
  display2ViewTEMP,
} from "../../utils/wildcardUtils/PotentialWildcardFunctions";
import { potentialWildcardLocatorAction } from "../../../actions/wildcardActions/subdomainWildcardAreasActions";

import { setLocalStorage } from "../../utils/setLocalStorage";

import { sublist3r } from "../../utils/wildcardUtils/sublist3r";
import { assetfinder } from "../../utils/wildcardUtils/assetfinder";
import { amass } from "../../utils/wildcardUtils/amass";

import Body from "./Body";
import NavBar from "./NavBar";

class WildCardApp extends Component {
  async componentDidMount() {
    if (!localStorage.getItem("potentialWildcardLocator")) {
      setTimeout(async () => {
        // Process & Display To DOM
        let result = await display2View(
          sublist3r,
          assetfinder,
          amass,
          "totalSubdomains",
          "combinedSubdomain",
          "totalPotentialWildCardArea",
          "combinedPotentialWildCardArea"
        );

        // Update Both State & LS With Result
        this.props.potentialWildcardLocatorAction(result);
        setLocalStorage("potentialWildcardLocator", result);

        // Save Input Values To LS
        setLocalStorage("sublist3rInput", sublist3r);
        setLocalStorage("assetfinderInput", assetfinder);
        setLocalStorage("amassInput", amass);

        // Upload Input Values Into Input Fields
        sublist3r.forEach((element) => {
          document.getElementById("sublist3rInput").value += element;
        });
        assetfinder.forEach((element) => {
          document.getElementById("assetfinderInput").value += element;
        });
        amass.forEach((element) => {
          document.getElementById("amassInput").value += element;
        });
      }, 10);
    } else {
      setTimeout(async () => {
        // Fetch Previous State Result From LS, Update DOM, & Update State
        let LSresult = JSON.parse(
          localStorage.getItem("potentialWildcardLocator")
        );
        let result = await display2ViewTEMP(
          "totalSubdomains",
          "combinedSubdomain",
          "totalPotentialWildCardArea",
          "combinedPotentialWildCardArea",
          LSresult.totalSubdomains,
          LSresult.wildCardAreas
        );

        this.props.potentialWildcardLocatorAction(result);
      }, 10);
    }
  }

  render() {
    return (
      <div>
        <NavBar />
        <Body />
      </div>
    );
  }
}

WildCardApp.propType = {
  potentialWildcardLocatorAction: PropTypes.func.isRequired,
};

export default connect(null, {
  potentialWildcardLocatorAction,
})(WildCardApp);
