import JSnumberToWordProcessor from "js-number-to-word-processor";

export async function initDisplay(generatedDns) {
  let generatedDnsLength = generatedDns.length.toString();
  let totalDnsGenerated;

  if (!generatedDnsLength.includes("e") || !generatedDnsLength.includes("+")) {
    totalDnsGenerated = await JSnumberToWordProcessor(generatedDnsLength)[0]
      .displayWholeNum;
  } else {
    totalDnsGenerated = generatedDnsLength;
  }

  document.getElementById("altDnsBadge1").innerText = totalDnsGenerated;
  document.getElementById("totalDnsGenerated").innerText = totalDnsGenerated;

  if (generatedDnsLength > 3000) {
    document.getElementById("generatedDns").textContent = "";
    document.getElementById("generatedDns").textContent =
      "For Sake Of Optimization;\n\nOutput Greater Than 3000 Would Only Be Available To View In 'Console Tab',\nAnd\nFor Download Through The 'Save Output To File' Button At The Top";
  } else {
    document.getElementById("generatedDns").textContent = "";
    generatedDns.forEach((element) => {
      document.getElementById("generatedDns").textContent += `${element}\n`;
    });
  }
}

export async function displayResolvedDns(dns, timeout1, timeout2) {
  let generatedDnsLength = dns.length.toString();
  let totalDnsGenerated;

  if (!generatedDnsLength.includes("e") || !generatedDnsLength.includes("+")) {
    totalDnsGenerated = await JSnumberToWordProcessor(generatedDnsLength)[0]
      .displayWholeNum;
  } else {
    totalDnsGenerated = generatedDnsLength;
  }

  document.getElementById("altDnsBadge2").innerText = totalDnsGenerated;
  if (dns.length > 3000) {
    document.getElementById("resolvedDns").textContent = "";
    document.getElementById("resolvedDns").textContent =
      "For Sake Of Optimization;\n\nOutput Greater Than 3000 Would Only Be Available To View In 'Console Tab',\nAnd\nFor Download Through The 'Save Output To File' Button At The Top";
  } else {
    document.getElementById("resolvedDns").textContent = "";
    dns.forEach((element) => {
      document.getElementById("resolvedDns").textContent += `${element}\n`;
    });
  }
}

