export const postBackendAPI = async (dns2Resolve) => {
  const options = {
    method: "POST",
    // credentials: "same-origin", // <-- includes cookies in the request
    body: JSON.stringify(dns2Resolve),
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
      // "CSRF-Token": token, // <-- is the csrf token as a header
    },
  };

  const response = await fetch(
    "http://localhost:5000/postDnsResolver",
    options
  );

  const body = await response.json();

  if (response.status !== 200) throw Error("body.message");

  return body;
};

export function saveFile(generatedDns, fileName) {
  let holder = [...generatedDns];
  let holderStr = holder.toLocaleString().replaceAll(",", "\n");
  return new File([holderStr], fileName, {
    type: "text/plain;charset=utf-8",
  });
}

export function readFile(e, wordInput) {
  var file = e.target.files[0];

  if (
    file.type !== "text/plain" &&
    file.name.slice(file.length - 4) !== ".txt"
  ) {
    return alert("Only '.txt' File Is Allowed");
  }

  document.getElementById(wordInput).value = "";

  var reader = new FileReader();
  reader.onload = function (progressEvent) {
    document.getElementById(wordInput).value = this.result;
  };
  reader.readAsText(file);
}
