// Infuse streamlined_1_ Domain With domain_2_ If There Are Of Same Wordings Else Maintain Both
function analyzeSubDomains(streamlined_1_, domain_2_) {
  let myPromise = new Promise(function (myResolve, myReject) {
    let replace;
    let boolOutput = false;

    let streamlined_1_Length = streamlined_1_.length;
    let domain_2_Length = domain_2_.length;

    if (streamlined_1_Length > domain_2_Length) {
      // Check if "." + domain_2_ is Achievable Within streamlined_1_
      if (
        streamlined_1_.substr(streamlined_1_Length - 1 - domain_2_Length) ===
        "." + domain_2_
      ) {
        // console.log(true + " >");
        replace = "do nothing";
        boolOutput = true;
        // console.log("");
      } else {
        // console.log(false + " >");
        replace =
          "add 2 to streamlined only if it doesn't exist in whatever form";
        boolOutput = false;
        // console.log("");
      }
    } else if (streamlined_1_Length < domain_2_Length) {
      // Check if "." + streamlined_1_ is Achievable Within domain_2_
      if (
        domain_2_.substr(domain_2_Length - 1 - streamlined_1_Length) ===
        "." + streamlined_1_
      ) {
        // console.log(true + " <");
        replace = "1 with 2";
        boolOutput = true;
        // console.log("");
      } else {
        // console.log(false + " <");
        replace =
          "add 2 to streamlined only if it doesn't exist in whatever form";
        boolOutput = false;
        // console.log("");
      }
    } else if (streamlined_1_Length === domain_2_Length) {
      // Both streamlined_1_ & domain_2_ Are Of Same length
      //  Check If Both Domains Are Same
      if (streamlined_1_ === domain_2_) {
        // console.log(true + " ===");
        replace = "do nothing";
        boolOutput = true;
        // console.log("");
      } else {
        // console.log(false + " ===");
        replace =
          "add 2 to streamlined only if it doesn't exist in whatever form";
        boolOutput = false;
        // console.log("");
      }
    }
    myResolve({
      replace,
      boolOutput,
    });
    myReject("An Error Occurred @ analyzeSubDomains Function");
  });
  return myPromise.then(
    (success) => success,
    (error) => error
  );
}

// Add domain2Add 2 to streamlinedArg only if it doesn't exist in whatever form
async function add2Streamlined(streamlinedArg, domain2Add) {
  let myPromise = new Promise(function (myResolve, myReject) {
    let matchFound = false;
    let sudoDomain2Add = "";
    let streamlined = streamlinedArg;

    for (let j = 0; j < streamlined.length; j++) {
      let currentStreamLiner_1_ = streamlined[j];
      let currentStreamLinerLength_1_ = currentStreamLiner_1_.length;
      let domain2AddLength = domain2Add.length;

      if (currentStreamLinerLength_1_ > domain2AddLength) {
        // Check if "." + domain2Add is Achievable Within currentStreamLiner_1_
        if (
          currentStreamLiner_1_.substr(
            currentStreamLiner_1_.length - domain2Add.length - 1
          ) ===
          "." + domain2Add
        ) {
          //  found a match. so discard as content of currentStreamLiner already encompass domain2Add
          matchFound = true;
          break;
        } else {
          // matchFound = false;
          if (sudoDomain2Add === "") {
            sudoDomain2Add = domain2Add;
          } else {
            // definitely sudoDomain2Add & domain2Add would have something in common
            if (sudoDomain2Add.length < domain2Add.length) {
              sudoDomain2Add = domain2Add;
            }
          }
        }
      } else if (currentStreamLinerLength_1_ < domain2AddLength) {
        // Check if "." + currentStreamLiner_1_ is Achievable Within domain2Add
        if (
          domain2Add.substr(
            domain2Add.length - currentStreamLiner_1_.length - 1
          ) ===
          "." + currentStreamLiner_1_
        ) {
          // found a match.
          // Need To Check domain2Add with all currentStreamLiner_1_
          matchFound = true;
          if (sudoDomain2Add === "") {
            sudoDomain2Add = domain2Add;
          } else {
            // definitely sudoDomain2Add & domain2Add would have something in common
            if (sudoDomain2Add.length < domain2Add.length) {
              sudoDomain2Add = domain2Add;
            }
          }
        } else {
          // matchFound = false;
          if (sudoDomain2Add === "") {
            sudoDomain2Add = domain2Add;
          } else {
            // definitely sudoDomain2Add & domain2Add would have something in common
            if (sudoDomain2Add.length < domain2Add.length) {
              sudoDomain2Add = domain2Add;
            }
          }
        }
      } else if (currentStreamLinerLength_1_ === domain2AddLength) {
        // Both currentStreamLiner_1_ & domain2Add Are Of Same length
        //  Check If Both Domains Are Same
        if (currentStreamLiner_1_ === domain2Add) {
          matchFound = true;
        } else if (currentStreamLiner_1_ !== domain2Add) {
          // matchFound = false;
          if (sudoDomain2Add === "") {
            sudoDomain2Add = domain2Add;
          } else {
            // definitely sudoDomain2Add & domain2Add would have something in common
            if (sudoDomain2Add.length < domain2Add.length) {
              sudoDomain2Add = domain2Add;
            }
          }
        }
      }
    }

    // ##################################((((())))########################################
    if (!matchFound) {
      if (sudoDomain2Add === "") {
        streamlined.push(domain2Add);
      } else {
        streamlinedMatcher();
      }
    } else {
      streamlinedMatcher();
    }

    // ##################################(((())))########################################

    function streamlinedMatcher() {
      let checkMatch = false;
      let sudoDomain2Add_2_ = "";
      let streamlinedLength = streamlined.length;
      let sudoDomain2AddLength = sudoDomain2Add.length;

      // ensure sudoDomain2Add doesn't exist in streamlined b4 pushing
      for (let k = 0; k < streamlinedLength; k++) {
        let streamed_1_ = streamlined[k];
        let streamed_1_Length = streamed_1_.length;

        if (streamed_1_Length > sudoDomain2AddLength) {
          // Check if "." + sudoDomain2Add is Achievable Within streamed_1_
          if (
            streamed_1_.substr(streamed_1_Length - sudoDomain2AddLength - 1) ===
            "." + sudoDomain2Add
          ) {
            checkMatch = true;
            break;
          } else {
            if (sudoDomain2Add_2_ === "") {
              sudoDomain2Add_2_ = sudoDomain2Add;
            } else {
              // definitely sudoDomain2Add_2_ & sudoDomain2Add would have something in common
              if (sudoDomain2Add_2_.length < sudoDomain2AddLength) {
                sudoDomain2Add_2_ = sudoDomain2Add;
              }
            }
          }
        } else if (streamed_1_Length < sudoDomain2AddLength) {
          // Check if "." + streamed_1_ is Achievable Within sudoDomain2Add
          if (
            sudoDomain2Add.substr(
              sudoDomain2AddLength - streamed_1_Length - 1
            ) ===
            "." + streamed_1_
          ) {
            streamlined[k] = sudoDomain2Add;
            checkMatch = true;
            break;
          } else {
            if (sudoDomain2Add_2_ === "") {
              sudoDomain2Add_2_ = sudoDomain2Add;
            } else {
              // definitely sudoDomain2Add_2_ & sudoDomain2Add would have something in common
              if (sudoDomain2Add_2_.length < sudoDomain2AddLength) {
                sudoDomain2Add_2_ = sudoDomain2Add;
              }
            }
          }
        } else if (streamed_1_Length === sudoDomain2AddLength) {
          // Both streamed_1_ & sudoDomain2Add Are Of Same length
          //  Check If Both Domains Are Same
          if (streamed_1_ === sudoDomain2Add) {
            checkMatch = true;
          } else if (streamed_1_ !== sudoDomain2Add) {
            // matchFound = false;
            if (sudoDomain2Add_2_ === "") {
              sudoDomain2Add_2_ = sudoDomain2Add;
            } else {
              // definitely sudoDomain2Add_2_ & sudoDomain2Add would have something in common
              if (sudoDomain2Add_2_.length < sudoDomain2AddLength) {
                sudoDomain2Add_2_ = sudoDomain2Add;
              }
            }
          }
        }
      }
      if (!checkMatch) {
        if (sudoDomain2Add_2_ !== "") {
          streamlined.push(sudoDomain2Add_2_);
        }
      }
    }
    myResolve(streamlined);
    myReject("An Error Occurred @ add2Streamlined Function");
  });
  return myPromise.then(
    (success) => success,
    (error) => error
  );
}

// ((((((((((((((((((((((((((((((((((((((((((((()))))))))))))))))))))))))))))))))))))))))))))
function dnsWildCardGuesser(subdomain) {
  let myPromise = new Promise(function (myResolve, myReject) {
    let splitSubdomainS = subdomain.split(".");
    let splitSubdomain = [];

    // Eliminate All Space
    splitSubdomainS.forEach((sub) => {
      if (sub !== "") splitSubdomain.push(sub);
    });

    let www = "";
    if (splitSubdomain[0] === "www".toLowerCase()) {
      splitSubdomain.splice(0, 1);
      www = "www.";
    }

    // (((((((((((((((((((((((((((((((((((Retain At This Sport)))))))))))))))))))))))))))))))))))
    let splitSubdomainLength = splitSubdomain.length;
    let potentialWildCard = [];
    // (((((((((((((((((((((((((((((((((((Retain At This Sport)))))))))))))))))))))))))))))))))))

    if (splitSubdomainLength > 2) {
      for (let l = 0; l < splitSubdomainLength - 1; l++) {
        if (l !== splitSubdomainLength - 1 && l !== splitSubdomainLength - 2) {
          splitSubdomain.splice(0, 1);

          let sudoWildCard = www + "*." + splitSubdomain.join(".");

          potentialWildCard.push(sudoWildCard);
        }
      }
    }

    myResolve(potentialWildCard);
    myReject("An Error Occurred @ dnsWildCardGuesser Function");
  });
  return myPromise.then(
    (success) => success,
    (error) => error
  );
}

// (((((((((((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))))))))))))

export const returnUniqueLineOfDomains = async (subDomainArg) => {
  // console.log("subDomainArg:", subDomainArg);
  let streamlined_1_ = [];
  let subDomain_2_ = subDomainArg;
  let subDomainLength_2_ = subDomain_2_.length;
  let potentialDnsWildCard = [];

  for (let i = 0; i < subDomainLength_2_; i++) {
    if (i === 0) {
      streamlined_1_.push(subDomain_2_[i]);

      // Save Potential Wildcards
      potentialDnsWildCard = await dnsWildCardGuesser(subDomain_2_[i]);
    } else {
      for (let h = 0; h < streamlined_1_.length; h++) {
        // Infuse streamlined_1_ With domain_2_ If There Are Of Same Wordings Else Maintain Both
        let analysis = await analyzeSubDomains(
          streamlined_1_[h],
          subDomain_2_[i]
        );

        if (analysis.replace === "do nothing") {
          // Do Nothing
        } else if (analysis.replace === "1 with 2") {
          streamlined_1_[h] = subDomain_2_[i];

          // Save Potential Wildcards Area
          let receivedWildCardGuess = await dnsWildCardGuesser(subDomain_2_[i]);
          let receivedWildCardGuessLengthLoop = receivedWildCardGuess.length;

          for (let i = 0; i < receivedWildCardGuessLengthLoop; i++) {
            if (!potentialDnsWildCard.includes(receivedWildCardGuess[i])) {
              potentialDnsWildCard.push(receivedWildCardGuess[i]);
            }
          }
        } else if (
          analysis.replace ===
          "add 2 to streamlined only if it doesn't exist in whatever form"
        ) {
          // Add subDomain[i] 2 to streamlined_1_ only if it doesn't exist in whatever form
          let receiveAdd2StreamLined = await add2Streamlined(
            streamlined_1_,
            subDomain_2_[i]
          );

          // Update streamlined_1_ With receiveAdd2StreamLined (Optional as it already auto-updates)
          streamlined_1_ = receiveAdd2StreamLined;

          // Save Potential Wildcards Area
          let receivedWildCardGuess = await dnsWildCardGuesser(subDomain_2_[i]);
          let receivedWildCardGuessLength = receivedWildCardGuess.length;
          for (let i = 0; i < receivedWildCardGuessLength; i++) {
            if (!potentialDnsWildCard.includes(receivedWildCardGuess[i])) {
              potentialDnsWildCard.push(receivedWildCardGuess[i]);
            }
          }
        }
      }
    }
  }
  return { streamlined_1_, potentialDnsWildCard };
};
