import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { saveAs } from "file-saver";
import { saveFile } from "../../utils/altdnsUtils/altDnsGeneratorFunctions";

class NavBar extends Component {
  saveTotalSubdomainToFileEvent = () => {
    saveAs(
      saveFile(this.props.totalSubdomains, "SWcLAltDnsGenTotalSubdomain.txt")
    );
  };

  saveWildcardAreasToFileEvent = () => {
    saveAs(
      saveFile(this.props.wildCardAreas, "SWcLAltDnsGenWildcardAreas.txt")
    );
  };

  render() {
    return (
      <React.Fragment>
        <nav
          className="navbar navbar-expand-lg navbar-nav-scroll fixed-top navbar-dark bg-dark"
          aria-label="Main navigation"
        >
          <div className="container-fluid clearfix">
            <button
              className="navbar-brand bg-dark"
              style={{ fontStyle: "italic", fontSize: "2.4em" }}
            >
              Subdomain Wildcard Locator.js
              <span
                className="float-end me-3"
                style={{
                  marginTop: "-0.08rem",
                  marginLeft: ".2rem",
                  color: "lightgray",
                  fontSize: "20px",
                }}
              >
                (01)
              </span>
            </button>
          </div>
          {/* ABOUT */}
          <div className="nav nav-underline">
            <Link
              to="/about"
              className="nav-link float-end me-5"
              style={{ fontStyle: "italic", fontSize: "1.7em" }}
            >
              About
            </Link>
          </div>
        </nav>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}
        <div className="nav-scroller bg-dark shadow-sm">
          <nav className="nav nav-underline" aria-label="Secondary navigation">
            <Link
              to="/altDnsApp"
              className="nav-link"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
            >
              AltDns Generator.js
            </Link>

            <button
              className="nav-link bg-dark ms-auto"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
              onClick={this.saveTotalSubdomainToFileEvent}
            >
              Save{" "}
              <span
                id="wildCardBadge1"
                className="badge bg-light text-dark rounded-pill align-text-bottom"
              ></span>{" "}
              Total Subdomains To File
              <div
                id="notifyMeOfTotalDns"
                className="float-end ms-1"
                title="Result Not Saved To Storage"
                style={{
                  width: "20px",
                  height: "80%",
                  backgroundColor: "red",
                  border: "1px solid red",
                  borderRadius: "100%",
                  display: "none",
                }}
              ></div>
            </button>
            <button
              className="nav-link bg-dark ms-auto me-5"
              style={{ fontStyle: "italic", fontSize: "1.1em" }}
              onClick={this.saveWildcardAreasToFileEvent}
            >
              Save{" "}
              <span
                id="wildCardBadge2"
                className="badge bg-light text-dark rounded-pill align-text-bottom"
              ></span>{" "}
              Wildcard Areas To File
              <div
                id="notifyMeOfWildcards"
                className="float-end ms-1"
                title="Result Not Saved To Storage"
                style={{
                  width: "20px",
                  height: "80%",
                  backgroundColor: "red",
                  border: "1px solid red",
                  borderRadius: "100%",
                  display: "none",
                }}
              ></div>
            </button>
          </nav>
        </div>
        {/* ((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))) */}

        <nav
          className="navbar navbar-expand-sm mb-2 clearfix"
          style={{
            backgroundColor: "limegreen",
            display: "block",
            marginTop: "-10px",
          }}
        >
          <h5
            style={{
              fontStyle: "italic",
              fontSize: "large",
              marginBottom: "-6px",
              marginLeft: "20px",
            }}
          >
            {" "}
            <span
              id="totalSubdomains"
              style={{ fontWeight: "lighter", fontSize: "2em" }}
            ></span>{" "}
            Total Subdomains for{" "}
            <span
              id="domainName"
              style={{ fontWeight: "lighter", fontSize: "2em" }}
            ></span>
          </h5>

          <h5
            style={{
              fontStyle: "italic",
              fontSize: "large",
              marginTop: "-1.5rem",
              marginBottom: "-6px",
              textAlign: "end",
              marginRight: "20px",
            }}
          >
            <span
              id="totalPotentialWildCardArea"
              style={{ fontWeight: "lighter", fontSize: "2em" }}
            ></span>{" "}
            <span className="me-3"> Potential Wildcard Areas </span>
          </h5>
        </nav>
      </React.Fragment>
    );
  }
}

NavBar.propType = {
  totalSubdomains: PropTypes.array.isRequired,
  wildCardAreas: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  totalSubdomains: state.wildCard.subDomainWildcardLocator.totalSubdomains,
  wildCardAreas: state.wildCard.subDomainWildcardLocator.wildCardAreas,
});

export default connect(mapStateToProps, {})(NavBar);
