import {
  ALT_DNS,
  DNS,
  CHECK_BOX,
  RESOLVED_DNS,
  UNRESOLVED_DNS_NETWORK_ISSUE,
  RE_RESOLVED_DNS,
} from "../../actions/altdnsActions/types";

const initialState = {
  altDnsList: [],
  dnsInputList: {
    wordList: [],
    dnsList: [],
  },
  checkBox: false,
  resolvedDns: [],
  unResolvedDnsNetworkIssue: [],
};

export default function altDnsGeneratorReducer(state = initialState, action) {
  switch (action.type) {
    case ALT_DNS:
      return {
        ...state,
        altDnsList: action.payload,
      };
    case DNS:
      return {
        ...state,
        dnsInputList: action.payload,
      };
    case CHECK_BOX:
      return {
        ...state,
        checkBox: !state.checkBox,
      };
    case RESOLVED_DNS:
      return {
        ...state,
        resolvedDns: action.payload,
      };
    case UNRESOLVED_DNS_NETWORK_ISSUE:
      return {
        ...state,
        unResolvedDnsNetworkIssue: action.payload,
      };
    case RE_RESOLVED_DNS:
      return {
        ...state,
        resolvedDns: [...state.resolvedDns, ...action.payload],
      };
    default:
      return state;
  }
}
