import React, { Component } from "react";

class MainBody extends Component {
  render() {
    return (
      <div className="col-md-9">
        <div className="row form">
          <div className="col-md-7 form-group">
            <div className="row">
              <div className="col-md-3"></div>
              <div
                className="bg-dark text-light m-0 p-0 col-md-8 h6"
                style={{
                  textAlign: "center",
                  width: "50%",
                  borderRadius: "15px",
                }}
              >
                Un-Resolved DNS
              </div>
              <div className="col-md-1"></div>
            </div>
            <textarea
              id="generatedDns"
              className="form-control bg-dark text-light"
              style={{
                boxShadow:
                  "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                borderRadius: "35px",
                fontSize: "larger",
                padding: "5px 2px 2px 25px",
              }}
              rows="24"
            ></textarea>
          </div>
          {/* (((((((((((((((((((((((((((( 2nd Side )))))))))))))))))))))))))))) */}
          <div className="col-md-5 form-group">
            <div className="row">
              <div className="col-md-3"></div>
              <div
                className="bg-dark text-light m-0 p-0 col-md-8 h6"
                style={{
                  textAlign: "center",
                  width: "50%",
                  borderRadius: "15px",
                }}
              >
                Resolved DNS
              </div>
              <div className="col-md-1"></div>
            </div>
            <textarea
              id="resolvedDns"
              className="form-control bg-dark text-light"
              style={{
                boxShadow:
                  "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                borderRadius: "35px",
                fontSize: "larger",
                padding: "5px 2px 2px 25px",
              }}
              rows="24"
            ></textarea>
          </div>
        </div>
      </div>
    );
  }
}

export default MainBody;
